"use client"

import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useState } from "react"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import FileUpload from "@/components/FileUpload"
import ImageEditor from "@/components/ImageEditor"

export default function Home() {
  const [prompt, setPrompt] = useState("")
  const [loading, setLoading] = useState(false)
  const [generatedImage, setGeneratedImage] = useState("")
  const [error, setError] = useState("")
  const [isEditing, setIsEditing] = useState(false)
  const [referenceFile, setReferenceFile] = useState<File | null>(null)

  const handleGenerate = async () => {
    if (!prompt) {
      setError("Please enter a prompt")
      return
    }

    setLoading(true)
    setError("")

    try {
      // Here we would integrate with an AI image generation API
      // For now, we'll simulate with a placeholder
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      // If there's a reference file, we would send it along with the prompt
      if (referenceFile) {
        console.log("Reference file:", referenceFile.name)
      }
      
      setGeneratedImage("https://images.pexels.com/photos/1762851/pexels-photo-1762851.jpeg")
    } catch (err) {
      setError("Failed to generate image. Please try again.")
    } finally {
      setLoading(false)
    }
  }

  const handleFileSelect = (file: File) => {
    setReferenceFile(file)
  }

  const handleSaveEdit = (editedImage: string) => {
    setGeneratedImage(editedImage)
    setIsEditing(false)
  }

  return (
    <main className="min-h-screen p-8 bg-gradient-to-b from-gray-900 to-black">
      <div className="max-w-4xl mx-auto space-y-8">
        <div className="text-center space-y-2">
          <h1 className="text-4xl font-bold text-white">AI Image Generator</h1>
          <p className="text-gray-400">Create amazing images with AI</p>
          <p className="text-sm text-yellow-400 mt-1">
            For best results, please use English prompts.
          </p>
        </div>

        <Card className="p-6 bg-gray-800 border-gray-700 relative">
          {loading && (
            <div className="absolute inset-0 bg-gray-800/50 backdrop-blur-sm flex flex-col items-center justify-center gap-4">
              <div className="flex gap-2">
                <div className="w-3 h-3 bg-blue-500 rounded-full animate-bounce [animation-delay:-0.3s]"></div>
                <div className="w-3 h-3 bg-blue-500 rounded-full animate-bounce [animation-delay:-0.15s]"></div>
                <div className="w-3 h-3 bg-blue-500 rounded-full animate-bounce"></div>
              </div>
              <div className="w-48 h-1.5 bg-gray-700 rounded-full overflow-hidden">
                <div className="h-full bg-blue-500 rounded-full animate-progress"></div>
              </div>
            </div>
          )}
          <div className="space-y-4">
            <div className="space-y-2">
              <Label className="text-white">Enter your prompt</Label>
              <Textarea
                placeholder="A futuristic city with flying cars and neon lights..."
                value={prompt}
                onChange={(e) => setPrompt(e.target.value)}
                className="h-32 bg-gray-700 border-gray-600 text-white focus:ring-2 focus:ring-blue-500"
                onKeyDown={(e) => {
                  if (e.key === 'Enter' && !e.shiftKey) {
                    e.preventDefault();
                    handleGenerate();
                  }
                }}
              />
            </div>

            <FileUpload
              onFileSelect={handleFileSelect}
              accept="image/*,.pdf,.doc,.docx"
              label="Upload Reference (Optional)"
            />

            {error && (
              <div className="text-red-500 text-sm">{error}</div>
            )}

            <Button
              onClick={handleGenerate}
              disabled={loading}
              className={`w-full relative ${loading ? 'bg-blue-700' : 'bg-blue-600 hover:bg-blue-700'} text-white`}
            >
              {loading ? (
                <>
                  <span className="opacity-0">Generate Image</span>
                  <div className="absolute inset-0 flex items-center justify-center gap-2">
                    <div className="w-2 h-2 bg-white rounded-full animate-bounce [animation-delay:-0.3s]"></div>
                    <div className="w-2 h-2 bg-white rounded-full animate-bounce [animation-delay:-0.15s]"></div>
                    <div className="w-2 h-2 bg-white rounded-full animate-bounce"></div>
                  </div>
                </>
              ) : (
                "Generate Image"
              )}
            </Button>
          </div>
        </Card>

        {generatedImage && (
          <Card className="p-6 bg-gray-800 border-gray-700">
            <div className="space-y-4">
              <h2 className="text-xl font-semibold text-white">Generated Image</h2>
              <div className="relative aspect-video">
                <img
                  src={generatedImage}
                  alt="Generated"
                  className="rounded-lg object-cover w-full h-full"
                />
              </div>
              <div className="flex gap-2">
                <Button
                  onClick={() => {
                    const link = document.createElement('a')
                    link.href = generatedImage
                    link.download = 'generated-image.jpg'
                    link.click()
                  }}
                  className="bg-blue-600 hover:bg-blue-700 text-white"
                >
                  Download
                </Button>
                <Button
                  onClick={() => setIsEditing(true)}
                  variant="outline"
                  className="text-white border-gray-600 hover:bg-gray-700"
                >
                  Edit
                </Button>
              </div>
            </div>
          </Card>
        )}

        {isEditing && generatedImage && (
          <ImageEditor
            imageUrl={generatedImage}
            onSave={handleSaveEdit}
            onClose={() => setIsEditing(false)}
          />
        )}
      </div>
    </main>
  )
}
