"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Slider } from "@/components/ui/slider"
import { Label } from "@/components/ui/label"

interface ImageEditorProps {
  imageUrl: string
  onSave: (editedImage: string) => void
  onClose: () => void
}

export default function ImageEditor({ imageUrl, onSave, onClose }: ImageEditorProps) {
  const [brightness, setBrightness] = useState(100)
  const [contrast, setContrast] = useState(100)
  const [saturation, setSaturation] = useState(100)

  const handleSave = () => {
    // Here we would implement actual image processing
    // For now, we'll just pass back the original image
    onSave(imageUrl)
  }

  return (
    <Card className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div className="bg-gray-800 p-6 rounded-lg max-w-4xl w-full mx-4 space-y-6">
        <h2 className="text-2xl font-bold text-white">Edit Image</h2>
        
        <div className="relative aspect-video">
          <img
            src={imageUrl}
            alt="Editing"
            className="rounded-lg object-contain w-full h-full"
            style={{
              filter: `brightness(${brightness}%) contrast(${contrast}%) saturate(${saturation}%)`
            }}
          />
        </div>

        <div className="space-y-4">
          <div className="space-y-2">
            <Label className="text-white">Brightness</Label>
            <Slider
              value={[brightness]}
              onValueChange={(value) => setBrightness(value[0])}
              min={0}
              max={200}
              step={1}
              className="w-full"
            />
          </div>

          <div className="space-y-2">
            <Label className="text-white">Contrast</Label>
            <Slider
              value={[contrast]}
              onValueChange={(value) => setContrast(value[0])}
              min={0}
              max={200}
              step={1}
              className="w-full"
            />
          </div>

          <div className="space-y-2">
            <Label className="text-white">Saturation</Label>
            <Slider
              value={[saturation]}
              onValueChange={(value) => setSaturation(value[0])}
              min={0}
              max={200}
              step={1}
              className="w-full"
            />
          </div>
        </div>

        <div className="flex justify-end gap-2">
          <Button
            variant="outline"
            onClick={onClose}
            className="text-white border-gray-600 hover:bg-gray-700"
          >
            Cancel
          </Button>
          <Button
            onClick={handleSave}
            className="bg-blue-600 hover:bg-blue-700 text-white"
          >
            Save Changes
          </Button>
        </div>
      </div>
    </Card>
  )
}
